import { memo } from 'react';
import { Navigate } from 'react-router-dom';


const PublicRoute = ({ children }) => {

    let ok = localStorage.getItem('ok_bosque') || false;
    if(ok == "false"){
        ok = false;
    }
    if(ok == "true"){
        ok = true;
    }
console.log(ok);

const lastPath = localStorage.getItem('lastPath') || '/admin/anuncios';
return ok
        ? <Navigate to={lastPath} />
        : children
}


export default memo(PublicRoute);
